/* eslint-disable */
import React,{useEffect, useState} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let Box = styled.div`
   padding: 20px;  
`;

let Title = styled.h4`
font-size: 3vw;
color: ${d => d.color}
`;


function Detail(xan){
  let[alert,alertCh] = useState(true);
  let [inputData, inputDataCh] = useState('');

  useEffect(()=>{
    let timer = setTimeout(()=>{
      alertCh(false)
    },2000)
    console.log('안녕');
    return ()=>{
      clearTimeout(timer)
    }
  },[alert]);
   
    let { id }  = useParams();
    let history = useHistory();

    let findItem = xan.shoes.find( x=> x.id== id);

    return(
      <div className="container">
          <Box>
           <Title color='seagreen' >아이고 귀찮 ^^</Title>    
          </Box>
     {inputData}
          <input onChange={(e)=>{inputDataCh(e.target.value)}}/> 
 
          {
           alert === true ? 
           <div className="my-alert">
            <p>재고가 얼마남지 않았습니다.?</p>
          </div> : null 
          }
         
      <div className="row">
        <div className="col-md-6">
          <img src={`https://codingapple1.github.io/shop/shoes${findItem.id + 1}.jpg`} width="100%"/>
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{findItem.title}</h4>
          <p>{findItem.content}</p>
          <p>{findItem.price}</p>
          <StockInfo stock={xan.stock}></StockInfo>

          <button className="btn btn-danger" onClick={ () => {xan.stockCh([9,11,12])}} >주문하기</button>
          <button className="btn btn-primary" onClick={()=>{
              history.goBack();
            //   history.goBack('/')
          }}>뒤로가기</button>
          </div>
        </div>
      </div>
    )
  }


  function StockInfo(xan){
    return(
      <p>재고 : {xan.stock[0]} </p>
    )

  }
  export default Detail;